import React, { useState } from 'react';
import chineseConv from 'chinese-conv';

const ZhConvertTool = () => {
  const [inputText, setInputText] = useState('');
  const [outputText, setOutputText] = useState('');
  const [mode, setMode] = useState('simplified'); // 

  const handleInputChange = (e) => {
    setInputText(e.target.value);
  };

  const handleModeChange = (e) => {
    setMode(e.target.value);
  };

  const handleConvert = () => {
    if (mode === 'simplified') {
      setOutputText(chineseConv.tify(inputText)); // 
    } else {
      setOutputText(chineseConv.sify(inputText)); // 
    }
  };

  return (
    <div style={styles.container}>
      <h2></h2>
      <div>
        <textarea
          rows="4"
          cols="50"
          value={inputText}
          onChange={handleInputChange}
          placeholder=""
        />
      </div>
      <div>
        <label>
          <input
            type="radio"
            value="simplified"
            checked={mode === 'simplified'}
            onChange={handleModeChange}
          />
          
        </label>
        <label>
          <input
            type="radio"
            value="traditional"
            checked={mode === 'traditional'}
            onChange={handleModeChange}
          />
          
        </label>
      </div>
      <div>
        <button onClick={handleConvert}></button>
      </div>
      <div>
        <textarea
          rows="4"
          cols="50"
          value={outputText}
          readOnly
          placeholder=""
        />
      </div>
    </div>
  );
};

const styles = {
    header: {
      backgroundColor: '#4CAF50',
      color: 'white',
      textAlign: 'center',
      padding: '10px 0',
    },
    main: {
      padding: '20px',
      maxWidth: '800px',
      margin: '0 auto',
    },
    a: {
      textDecoration: 'none',
      fontWeight: 'bold',
      cursor: 'pointer',
      color: 'white',
    },
    container: {
      padding: "20px",
      maxWidth: "100%",
      margin: "0 auto",
      backgroundColor: "#f9f9f9",
      borderRadius: "8px",
      boxShadow: "0px 0px 10px rgba(0, 0, 0, 0.1)",
    },
  };
export default ZhConvertTool;
